@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat-regular.woff2") format("woff2");
  src: url("../fonts/montserrat-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat-medium.woff2") format("woff2");
  src: url("../fonts/montserrat-medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2");
  src: url("../fonts/montserrat-semibold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/montserrat-bold.woff2") format("woff2");
  src: url("../fonts/montserrat-bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #011c40;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  min-width: 320px;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #011c40;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

/* STATES & MODES
--------------------------------------------- */
HTML[data-state=off] {
  background: none;
}

HTML[data-state=off] BODY {
  opacity: 1 !important;
  filter: none;
}

BODY[data-filter=invert] {
  filter: invert(1);
}

/* CONTROLS
--------------------------------------------- */
.controls-panel {
  position: fixed;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  min-width: 175px;
  padding: 5px;
  box-sizing: border-box;
  border: 2px solid hsla(0deg, 0%, 0%, 0.25);
  border-radius: 5px;
  background: hsla(0deg, 0%, 100%, 0.9);
  box-shadow: 0 0 5px hsla(0deg, 0%, 0%, 0.5);
  font: 14px/1.2 Arial, sans-serif;
  text-transform: none;
  outline: none;
  color: #000;
}

.controls-panel__control {
  margin-right: 5px;
  padding: 3px;
  border: 2px solid hsla(0deg, 0%, 0%, 0.25);
  border-radius: 5px;
  outline: none;
  vertical-align: middle;
  font: inherit;
}

.controls-panel__control:focus {
  box-shadow: 0 0 0 3px hsl(75deg, 100%, 45%);
}

.controls-panel__control:disabled {
  background: transparent;
  opacity: 0.35;
  cursor: default;
}

.controls-panel__control--button {
  padding-left: 5px;
  padding-right: 5px;
  color: hsla(0deg, 0%, 0%, 0.9);
  cursor: pointer;
}

.controls-panel__control--button:enabled {
  background: yellowgreen;
  box-shadow: 0 2px 4px hsla(0deg, 0%, 0%, 0.4) inset, 0 -4px 4px hsl(90deg, 100%, 70%) inset;
}

.controls-panel__control--button:enabled:focus {
  background: yellowgreen;
  box-shadow: 0 2px 4px hsla(0deg, 0%, 0%, 0.4) inset, 0 -4px 4px hsl(90deg, 100%, 70%) inset, 0 0 0 3px hsl(75deg, 100%, 45%);
}

.controls-panel__control--button[data-state-num="0"]:enabled {
  background: #DDD;
  box-shadow: none;
  opacity: 0.8;
}

.controls-panel__control--number {
  max-width: 2.7em;
  padding-right: 0;
  box-sizing: content-box;
  text-align: center;
}

.controls-panel__control--number:focus {
  border-color: hsla(0deg, 0%, 0%, 0.35);
}

.controls-panel__control--drag-n-drop {
  position: relative;
  width: 19px;
  min-height: 1.8em; /* IE10 fixes */
  margin: 0;
  padding: 0;
  background: transparent;
  cursor: move;
}

.controls-panel__control--drag-n-drop:active {
  background: yellowgreen;
  box-shadow: 0 2px 4px hsla(0deg, 0%, 0%, 0.4) inset, 0 -4px 4px hsl(90deg, 100%, 70%) inset, 0 0 0 3px hsl(75deg, 100%, 45%);
  cursor: -webkit-grabbing;
  cursor: -moz-grabbing;
  cursor: grab;
}

.controls-panel__control--drag-n-drop::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 3px;
  height: 3px;
  border-radius: 50%;
  background: hsla(0deg, 0%, 0%, 0.5);
  box-shadow: 0 6px 0 hsla(0deg, 0%, 0%, 0.5), 0 -6px 0 hsla(0deg, 0%, 0%, 0.5);
  pointer-events: none;
}

