  }
}

.page-header__toggle-label {
  display: none;
}
@media (max-width: 767px) {
  .page-header__toggle-label {
    cursor: pointer;
    width: 24px;
    height: 25px;
    position: absolute;
    right: 15px;
    z-index: 1;
    display: block;
  }
  .page-header__toggle-label::before {
    content: "";
    position: absolute;
    top: 5px;
    left: 0;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    box-shadow: 0 6px 0 0 #f9fbfd, 0 12px 0 0 #f9fbfd;
  }
}

@media (max-width: 767px) {
  .page-header__toggle[type=checkbox]:checked ~ .page-header__nav-list {
    display: block;
  }
}

@media (max-width: 767px) {
  .page-header__toggle[type=checkbox]:checked ~ .page-header__toggle-label {
    top: 19px;
    right: 9px;
  }
  .page-header__toggle[type=checkbox]:checked ~ .page-header__toggle-label::before, .page-header__toggle[type=checkbox]:checked ~ .page-header__toggle-label::after {
    content: "";
    position: absolute;
    top: 11px;
    left: 2px;
    width: 18px;
    height: 2px;
    background-color: #011c40;
  }
  .page-header__toggle[type=checkbox]:checked ~ .page-header__toggle-label::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .page-header__toggle[type=checkbox]:checked ~ .page-header__toggle-label::after {
    transform: rotate(-45deg);
  }
}

